body {
  min-height: 91.5vh;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(63, 81, 181, 1)), to(rgba(0, 0, 0, 0.5))),
    url("./photo_74954_20160905.jpg");
  background-image: -webkit-linear-gradient(rgba(63, 81, 181, 1), rgba(0, 0, 0, 0.5)),
    url("./photo_74954_20160905.jpg");
  background-image: -o-linear-gradient(rgba(63, 81, 181, 1), rgba(0, 0, 0, 0.5)),
    url("./photo_74954_20160905.jpg");
  background-image: linear-gradient(rgba(63, 81, 181, 1), rgba(0, 0, 0, 0.5)),
    url("./photo_74954_20160905.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

.test__wrapper {
  padding-top: 1.5rem;
  display: -ms-flexbox;
  display: flex;
}

.test__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.test__module {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 1.35rem;
  font-weight: 600;
  text-align: center;
  margin: 0 0 0.5rem;
  padding: 0 1rem;
}

.test__testname {
  font-size: 1.15rem;
  color: #ffffff;
  text-align: center;
  margin: 0 0 1rem;
  padding: 0 1rem;
}

.test__cards {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-preferred-size: 92%;
      flex-basis: 92%;
  -ms-flex-pack: center;
      justify-content: center;
}

.test__buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: auto;
}
.test__btn {
  width: 92%;
  max-width: 18.6rem;
  margin-left: 0;
  margin-right: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding-right: 1rem;
  padding-left: 1rem;
  background-color: #526db5;
  color: #fff;
  font-size: 3rem;
  line-height: 4.2rem;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  box-shadow: none;
  -webkit-box-shadow: none;
  text-decoration: none;
}

.test__btn:hover {
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.39);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.39);
}

.test__notfound {
  display: inline-block;
  max-width: 18px;
  padding: 1.5rem;
  font-family: "Roboto", Arial, sans-serif;
  color: #000;
  background-color: #ddd;
  margin: auto;
}

@media (min-width: 768px) {
  .test__module {
    font-size: 2rem;
  }

  .test__testname {
    font-size: 1.6rem;
    margin-bottom: 2rem;
  }

  .test__cards {
    width: 92%;
    margin: 0 auto 1.5rem;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .test__btn {
  }
}

@media (min-width: 1024px) {
  .test__content {
    display: -ms-flexbox;
    display: flex;
  }

  .test__cards {
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }

  .test__btn {
    margin-bottom: 1.5rem;
  }
}



