.formCont {
  padding: 25px 25px 40px 25px;
  border-radius: 4px;
  width: 300px;
  font-size: 19px;
  margin: auto;
  -webkit-box-shadow: 7px 0px 40px -8px rgba(112, 111, 111, 1);
  box-shadow: 7px 0px 40px -8px rgba(112, 111, 111, 1);
}

.entSpan {
  color: #3f51b5;
  font-weight: 600;
  font-size: 19px;
  margin-bottom: 30px;
  text-align: center;
}

.input {
  display: block;
  width: 86%;
  border: none;
  font-size: 16px;
  outline: none;
  border-bottom: 2px solid #b8b8b9;
  color: #b8b8b9;
  margin-bottom: 30px;
}

.emCont {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.emSvg {
  width: 20px;
  height: 20px;
}

.lockCont {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.lockSvg {
  width: 20px;
  height: 20px;
}

.input:focus {
  border-bottom: 2px solid #f7a5a8;
}

.input::-webkit-input-placeholder {
  color: #b8b8b9;
}

.input:-ms-input-placeholder {
  color: #b8b8b9;
}

.input::-ms-input-placeholder {
  color: #b8b8b9;
}

.input::placeholder {
  color: #b8b8b9;
}

.styleCheckbox + label {
  cursor: pointer;
  outline: none;
}

.btn {
  font-size: 16px;
  font-weight: 600;
  outline: none;
  border: none;
  background-color: #ff5722;
  color: #fff;
  border-radius: 18px;
  padding: 10px 30px;
  cursor: pointer;
  width: 100%;
}

.btn:hover {
  background-color: #f77751;
}

.hiddenError {
  opacity: 0;
}

.showError {
  opacity: 1;
}

@media (min-width: 768px) {
  .formCont {
    width: 420px;
  }
}



