.regSpan {
  color: #3f51b5;
  font-weight: 600;
  font-size: 19px;
  margin-bottom: 30px;
  text-align: center;
}

.input {
  display: block;
  width: 86%;
  border: none;
  font-size: 16px;
  outline: none;
  border-bottom: 2px solid #b8b8b9;
  color: #b8b8b9;
  margin-bottom: 30px;
}

.emCont {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.emSvg {
  width: 20px;
  height: 20px;
}

.lockCont {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.lockSvg {
  width: 20px;
  height: 20px;
}

.input:focus {
  border-bottom: 2px solid #f7a5a8;
}

.input::-webkit-input-placeholder {
  color: #b8b8b9;
}

.input:-ms-input-placeholder {
  color: #b8b8b9;
}

.input::-ms-input-placeholder {
  color: #b8b8b9;
}

.input::placeholder {
  color: #b8b8b9;
}

.styleCheckbox + label {
  cursor: pointer;
  outline: none;
}

.agreement {
  color: #b8b8b9;
  font-size: 13px;
  text-align: center;
  margin-bottom: 16px;
}

.orangeSp {
  color: #ff5722;
  font-weight: 500;
  text-decoration: underline;
  cursor: pointer;
}

.btn {
  font-size: 16px;
  font-weight: 600;
  outline: none;
  border: none;
  background-color: #ff5722;
  color: #fff;
  border-radius: 18px;
  padding: 10px;
  cursor: pointer;
  width: 100%;
}

.btn:hover {
  background-color: #f77751;
}

.back {
  font-size: 2rem;
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 8px;
}



