.result__container {
  padding: 1.5rem;
  margin: 0 auto 1.15rem;
  width: 91%;
  border-radius: 4px;
  background-color: #fff;
  -webkit-box-shadow: 3px 0px 18px -1px rgba(0, 0, 0, 0.75);
  box-shadow: 3px 0px 18px -5px rgba(0, 0, 0, 0.75);
}

.result__content {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
}

.result__x {
  position: absolute;
  top: 0.4rem;
  right: 0.5rem;
  font-size: 1.4rem;
  color: #fc5830;
  cursor: pointer;
}

.result__header {
  color: #4356b3;
  margin-bottom: 1.5rem;
}

.result__list {
  padding: 0;
  list-style: none;
  margin-bottom: 1.5rem;
}

.result__item {
  margin-bottom: 1rem;
  font-size: 1rem;
}

.result__item:last-child {
  margin-bottom: 0;
}

.result__btn {
  color: #fff;
  font-size: 1rem;
  padding: 0.4rem 0.8rem;
  display: block;
  margin: auto;
  background-color: #4356b3;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
}

.result__btn:hover {
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.39);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.39);
}

@media (min-width: 1024px) {
  .result__container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    min-width: -webkit-max-content;
    min-width: -moz-max-content;
    min-width: max-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 0 1.25rem;
  }

  .result__header {
    font-size: 1.6rem;
  }

  .result__list {
    font-size: 1.3rem;
  }

  .result__btn {
    font-size: 1.5rem;
  }
}



